.jumbotron.minGobJumbo {
    background-image: url(https://www.buenosaires.gob.ar/sites/gcaba/files/gopr0208_0.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    padding: 0;
}
.minGobJumboOverlay {
    background-color: rgba(0, 0, 0, 0.4);
}
.jumbotron.minGobJumbo .breadcrumb {
    background-color: rgba(0,0,0,0.4);
    padding: 20px 0;
    font-size: 12px;
    line-height: 16px;
    text-align: left;
}
.jumbotron.minGobJumbo .breadcrumb .container {
    padding: 0 15px;
    display: flex;
    flex-direction: row;
    text-transform: capitalize;
}
.jumbotron.minGobJumbo .breadcrumb .container a {
    color: #ffffff !important;
    font-size: 14px;
    text-decoration: none;
}
.jumbotron.minGobJumbo .area-title {
    color:#ffffff;
    text-align: left;
    padding-top: 48px;
    padding-bottom: 24px;
}
.jumbotron.minGobJumbo .area-title h1 {
    font-size: 64px;
}
.jumbotron.minGobJumbo .area-title p {
    font-size: 24px;
    font-weight: 200;
}
.jumbotron.minGobJumbo .navbar {
    padding: 0;
}
.jumbotron.minGobJumbo .navbar .container {
    padding: 0;
    border: 0;
    background-color: transparent;
}
.jumbotron.minGobJumbo .navbar .container .navbar-nav a.nav-link {
    color: rgba(255,255,255,0.8);
    font-size: 16px !important;
    font-weight: bold;
    padding:15px;
}

.jumbotron.minGobJumbo .minGobJumboOverlay .container {
    padding-bottom: 0;
    padding-top: 0;
}

